.small{
    flex: 1;
    -webkit-box-shadow: 0px 0px 9px -5px #000000; 
    box-shadow: 0px 0px 9px -5px #000000;
}
.widgetsmTitle{
    margin: 15px 15px;
}
.widgetsmUser{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 7px;
    padding: 11px;
}
.widgetsmUser img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
}
.widgetsmList{
    list-style: none;
    display: flex;
}
.btnDisplay{
    margin-left: 15px;
    padding: 4px 15px;
    border: 1px solid #555;
    border-radius: 7px;
    display: flex;
    justify-content: center;
    cursor: pointer;
}
.txtDisplay{
    margin-left: 5px;
    color: #555;
}