.feature{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.featureItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 30px;
    flex: 1;
    margin: 0 20px;
    -webkit-box-shadow: 0px 0px 9px -5px #000000; 
    box-shadow: 0px 0px 9px -5px #000000;
    border-radius: 10px;
    background-color: #fff;
}
.featureContainer{
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
    align-items: center;
}
.featureSales{
    font-size: 20px;
}
.downArrow{
    color: red;
}
.upArrow{
    color: green;
}
.percentSale{
    display: flex;
    margin-left: 15px;
}
.month{
    font-size: 12px;
    color: #555;
}