.userList{
    flex: 4;
    text-align: right;
}
.userListUser img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.userListUser{
    display: flex;
    align-items: center;
}
.btnEdit{
    color: white;
    cursor: pointer;
    border-radius: 10px;
    padding: 5px 10px;
    background-color: #78c9a2;
    border: 1px solid #fff;
}
.btnEdit:hover{
    opacity: 0.7;
    color: black;
}
.btnDelete{
    color: red;
    cursor: pointer;
}
.btnDelete:hover{
    opacity: 0.7;
    color: black;
}