.topbar{
    width: 100%;
    height: 50px;
    position: sticky;
    top: 0;
    z-index: 999;
    background-color: rgb(208, 236, 218);
}
.topbarWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    height: 100%;
}
.logo{
    font-size: 1.8rem;
    color: rgb(34, 151, 150);
    font-weight: bold;
    cursor: pointer;
}
.left{
    display: flex;
    align-items: center;
    margin-top: 15px;
}
.iconTopbarContainer{
    position: relative;
    margin-right: 5px;
    cursor: pointer;
    margin-right: 10px;
    color: #555;
}
.yellow{
    background-color: rgb(163, 163, 11) !important;
}
.iconTopbarContainer .numberbadge{
    position: absolute;
    top: -8px;
    right: 11px;
    background-color: red;
    border-radius: 50%;
    color: white;
    padding: 0px 5px;
    font-size: 13px;
}
.avatar{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}