.sidebar{
    display: flex;
    /* flex: 1; */
    height: calc(100vh - 50px);
    background-color: rgba(255, 255, 255, 0.7);
    position: sticky;
    top: 50px;
}
.sidebarWrapper{
    padding: 20px;
    color: #555;
}
.sidebarMenu{
    margin-bottom: 7px;
}
.sidebarTitle{
    font-size: 15px;
    color: rgb(36, 27, 27);
    text-align: right;
}
.sidebarList{
    list-style: none;
    padding: 8px;
}
.sidebarListItem{
    display: flex;
    justify-content: space-between;
    align-content: center;
    width: 120px;
    margin-bottom: 11px;
    cursor: pointer;
    padding: 3px 10px;
    border-radius: 8px;
}
.sidebarListItem.active,.sidebarListItem:hover{
    background-color: rgba(179, 201, 143, 0.2);
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}
.sidebarIcon{
    font-size: 14px;
    color: rgb(179, 201, 143);
}