.large{
    flex: 2;
    -webkit-box-shadow: 0px 0px 9px -5px #000000; 
    box-shadow: 0px 0px 9px -5px #000000;
    padding: 20px;
}
.widgetlargeTitle{
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 10px;
}
.widgetLatgeTh{
    width: 25%;
}
.widgetTabelLarge {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .widgetLatgeTd, .widgetLatgeTh {
    border: 1px solid #dddddd;
    text-align: right;
    padding: 8px;
    width:220px
  }
  .widgetLatgeTd .approved{
      background-color: rgb(69, 167, 111);
      outline: none;
      border: 1px solid #555;
      border-radius: 7px;
      padding: 5px;
      cursor: pointer;
  }

  .widgetLatgeTd .cancel{
      background-color: rgb(223, 66, 126);
      outline: none;
      border: 1px solid #555;
      border-radius: 7px;
      padding: 5px;
      cursor: pointer;
  }